/* eslint-disable @typescript-eslint/no-explicit-any */
/* eslint-disable @typescript-eslint/no-unused-vars */
// app/organizer/OrganizerClientLayout.tsx
'use client';

import React, { useEffect, useState } from 'react';
import { useTheme, useThemeClasses } from '@/hooks/useTheme';
import { useAuth } from '@/hooks/useAuth';
import { useI18n } from '@/hooks/useSafeI18n';
import { useRouter } from 'next/navigation';
import { userApi } from '@/lib/api';
import {
    Calendar,
    Users,
    DollarSign,
    BarChart3,
    Settings,
    Plus,
    Menu,
    X,
    Home,
    MapPin,
    Ticket,
    LogOut,
    Languages
} from 'lucide-react';

// Add the missing interface - extract from your api.ts
interface UserPreferences {
    emailNotifications: boolean;
    smsNotifications: boolean;
    newBookingNotifications: boolean;
    cancellationNotifications: boolean;
    lowInventoryNotifications: boolean;
    dailyReports: boolean;
    weeklyReports: boolean;
    monthlyReports: boolean;
    twoFactorEnabled: boolean;
    sessionTimeout: number;
    loginNotifications: boolean;
    defaultTimeZone?: string;
    defaultEventDuration: number;
    defaultTicketSaleStart: number;
    defaultRefundPolicy?: string;
    requireApproval: boolean;
    autoPublish: boolean;
    theme: string;
    language: string;
    dateFormat: string;
    timeFormat: string;
    currency: string;
    accentColor: string;
    fontSize: string;
    compactMode: boolean;
}

// Add the safe mapping functions
const safeMapTheme = (theme: string | undefined): 'light' | 'dark' | 'auto' => {
    if (!theme) return 'auto';
    return ['light', 'dark', 'auto'].includes(theme) ? theme as 'light' | 'dark' | 'auto' : 'auto';
};

const safeMapFontSize = (fontSize: string | undefined): 'small' | 'medium' | 'large' => {
    if (!fontSize) return 'medium';
    return ['small', 'medium', 'large'].includes(fontSize) ? fontSize as 'small' | 'medium' | 'large' : 'medium';
};

interface OrganizerClientLayoutProps {
    children: React.ReactNode;
}

const OrganizerClientLayout: React.FC<OrganizerClientLayoutProps> = ({ children }) => {
    const { user, logout, isOrganizer, isLoading } = useAuth();
    const router = useRouter();
    const [sidebarOpen, setSidebarOpen] = useState(false);
    const [languageDropdownOpen, setLanguageDropdownOpen] = useState(false);
    const [preferencesLoaded, setPreferencesLoaded] = useState(false);

    // Theme hooks
    const { isDark, isCompact, updateTheme, initializeTheme } = useTheme();
    const themeClasses = useThemeClasses();

    // I18n hook
    const { t, changeLanguage, currentLanguage, supportedLanguages } = useI18n();

    // Load user preferences when component mounts
    useEffect(() => {
        const loadUserPreferences = async () => {
            if (user && isOrganizer) {
                try {
                    const preferences = await userApi.getPreferences();

                    // Apply theme preferences with safe mapping
                    updateTheme({
                        theme: safeMapTheme(preferences.theme),
                        fontSize: safeMapFontSize(preferences.fontSize),
                        accentColor: preferences.accentColor || 'blue',
                        compactMode: Boolean(preferences.compactMode)
                    });

                    // Apply language preference
                    if (preferences.language && preferences.language !== currentLanguage) {
                        changeLanguage(preferences.language);
                    }

                    setPreferencesLoaded(true);
                } catch (error) {
                    console.error('Failed to load user preferences:', error);
                    // Initialize with defaults if loading fails
                    initializeTheme();
                    setPreferencesLoaded(true);
                }
            } else {
                initializeTheme();
                setPreferencesLoaded(true);
            }
        };

        loadUserPreferences();
    }, [user, isOrganizer, updateTheme, changeLanguage, currentLanguage, initializeTheme]);

    // Handle authentication and authorization
    useEffect(() => {
        if (!isLoading && preferencesLoaded) {
            if (!user) {
                router.push('/login');
                return;
            }

            if (!isOrganizer) {
                router.push('/');
                return;
            }
        }
    }, [user, isOrganizer, isLoading, preferencesLoaded, router]);

    const navigation = [
        {
            name: t('dashboard'),
            href: '/organizer/dashboard',
            icon: Home,
        },
        {
            name: t('events'),
            href: '/organizer/events',
            icon: Calendar,
        },
        {
            name: t('createEvent'),
            href: '/organizer/events/create',
            icon: Plus,
        },
        {
            name: 'Venues',
            href: '/organizer/venues',
            icon: MapPin,
        },
        {
            name: 'Ticket Management',
            href: '/organizer/tickets',
            icon: Ticket,
        },
        {
            name: 'Analytics',
            href: '/organizer/analytics',
            icon: BarChart3,
        },
        {
            name: t('settings'),
            href: '/organizer/settings',
            icon: Settings,
        }
    ];

    const handleLogout = () => {
        logout();
        router.push('/login');
    };

    const handleLanguageChange = async (langCode: string) => {
        changeLanguage(langCode);
        setLanguageDropdownOpen(false);

        try {
            // Create a properly typed partial update object
            const updateData: Partial<UserPreferences> = {
                language: langCode
            };
            await userApi.updatePreferences(updateData);
        } catch (error) {
            console.error('Failed to save language preference:', error);
        }
    };

    // Show loading state while preferences are loading
    if (isLoading || !preferencesLoaded) {
        return (
            <div className={`min-h-screen ${themeClasses.themeBg} flex items-center justify-center theme-transition`}>
                <div className="text-center">
                    <div className="animate-spin rounded-full h-12 w-12 border-b-2 accent-border mx-auto"></div>
                    <p className={`mt-4 ${themeClasses.themeMutedFg}`}>{t('loading')}</p>
                </div>
            </div>
        );
    }

    // Don't render anything if user is not authenticated or not an organizer
    if (!user || !isOrganizer) {
        return null;
    }

    return (
        <div className={`h-screen flex overflow-hidden ${themeClasses.themeBg} theme-transition`}>
            {/* Mobile sidebar overlay */}
            {sidebarOpen && (
                <div className="fixed inset-0 z-40 lg:hidden">
                    <div
                        className="fixed inset-0 bg-gray-600 bg-opacity-75"
                        onClick={() => setSidebarOpen(false)}
                    />
                </div>
            )}

            {/* Sidebar with theme support */}
            <div className={`fixed inset-y-0 left-0 z-50 w-64 ${themeClasses.themeCard} shadow-lg transform ${sidebarOpen ? 'translate-x-0' : '-translate-x-full'
                } transition-transform duration-300 ease-in-out lg:translate-x-0 lg:static lg:inset-0 flex flex-col theme-transition`}>

                {/* Sidebar header with theme support */}
                <div className={`flex items-center justify-between ${isCompact ? 'h-12' : 'h-16'} px-6 border-b ${themeClasses.themeBorder} flex-shrink-0`}>
                    <div className="flex items-center">
                        <div className="flex-shrink-0">
                            <Calendar className={`${isCompact ? 'h-6 w-6' : 'h-8 w-8'} accent-text`} />
                        </div>
                        <div className="ml-3">
                            <h1 className={`${isCompact ? themeClasses.textBase : themeClasses.textLg} font-semibold ${themeClasses.themeFg}`}>EventPro</h1>
                            <p className={`text-xs ${themeClasses.themeMutedFg}`}>Organizer</p>
                        </div>
                    </div>
                    <button
                        onClick={() => setSidebarOpen(false)}
                        className={`lg:hidden p-2 rounded-md ${themeClasses.themeMutedFg} ${themeClasses.hover} transition-colors`}
                    >
                        <X className="h-5 w-5" />
                    </button>
                </div>

                {/* Navigation with theme support */}
                <nav className={`flex-1 px-4 ${isCompact ? 'py-4' : 'py-6'} ${isCompact ? 'space-y-1' : 'space-y-2'} overflow-y-auto`}>
                    {navigation.map((item) => {
                        const Icon = item.icon;
                        const isActive = typeof window !== 'undefined' && window.location.pathname === item.href;

                        return (
                            <a
                                key={item.name}
                                href={item.href}
                                onClick={() => setSidebarOpen(false)}
                                className={`flex items-center ${isCompact ? 'px-2 py-1.5' : 'px-3 py-2'} ${themeClasses.textSm} font-medium rounded-lg transition-colors ${isActive
                                    ? 'accent-bg text-white'
                                    : `${themeClasses.themeMutedFg} ${themeClasses.hover} hover:${themeClasses.themeFg}`
                                    }`}
                            >
                                <Icon className={`${isCompact ? 'h-4 w-4 mr-2' : 'h-5 w-5 mr-3'} ${isActive ? 'text-white' : themeClasses.themeMutedFg}`} />
                                <span className={isCompact ? 'text-xs' : ''}>{item.name}</span>
                            </a>
                        );
                    })}
                </nav>

                {/* Language Selector */}
                <div className={`border-t ${themeClasses.themeBorder} ${isCompact ? 'p-2' : 'p-4'} flex-shrink-0`}>
                    <div className="relative">
                        <button
                            onClick={() => setLanguageDropdownOpen(!languageDropdownOpen)}
                            className={`flex items-center w-full ${isCompact ? 'px-2 py-1.5' : 'px-3 py-2'} ${themeClasses.textSm} ${themeClasses.themeMutedFg} ${themeClasses.hover} hover:${themeClasses.themeFg} rounded-lg transition-colors`}
                        >
                            <Languages className={`${isCompact ? 'h-3 w-3 mr-1' : 'h-4 w-4 mr-2'}`} />
                            <span className={isCompact ? 'text-xs' : ''}>{t('language')}</span>
                        </button>

                        {languageDropdownOpen && (
                            <div className={`absolute bottom-full left-0 mb-2 w-full ${themeClasses.themeCard} rounded-lg shadow-lg border ${themeClasses.themeBorder} py-1 z-50`}>
                                {supportedLanguages.map((lang) => (
                                    <button
                                        key={lang.code}
                                        onClick={() => handleLanguageChange(lang.code)}
                                        className={`flex items-center w-full ${isCompact ? 'px-2 py-1.5' : 'px-3 py-2'} ${themeClasses.textSm} ${currentLanguage === lang.code
                                            ? `${themeClasses.themeFg} accent-bg accent-text`
                                            : `${themeClasses.themeMutedFg} ${themeClasses.hover} hover:${themeClasses.themeFg}`
                                            } transition-colors`}
                                    >
                                        <span className={`${isCompact ? 'mr-1' : 'mr-2'}`}>{lang.flag}</span>
                                        <span className={isCompact ? 'text-xs' : ''}>{lang.nativeName}</span>
                                    </button>
                                ))}
                            </div>
                        )}
                    </div>
                </div>

                {/* User profile section with theme support */}
                <div className={`border-t ${themeClasses.themeBorder} ${isCompact ? 'p-2' : 'p-4'} flex-shrink-0`}>
                    <div className={`flex items-center ${isCompact ? 'space-x-2 mb-1' : 'space-x-3 mb-3'}`}>
                        <div className="flex-shrink-0">
                            <div className={`${isCompact ? 'h-6 w-6' : 'h-10 w-10'} rounded-full accent-bg flex items-center justify-center`} style={{ opacity: 0.1 }}>
                                <Users className={`${isCompact ? 'h-3 w-3' : 'h-5 w-5'} accent-text`} />
                            </div>
                        </div>
                        <div className="flex-1 min-w-0">
                            <p className={`${isCompact ? 'text-xs' : themeClasses.textSm} font-medium ${themeClasses.themeFg} truncate`}>
                                {user.firstName} {user.lastName}
                            </p>
                            <p className={`text-xs ${themeClasses.themeMutedFg} truncate ${isCompact ? 'hidden' : ''}`}>{user.email}</p>
                        </div>
                    </div>
                    <button
                        onClick={handleLogout}
                        className={`flex items-center w-full ${isCompact ? 'px-2 py-1.5' : 'px-3 py-2'} ${themeClasses.textSm} ${themeClasses.themeMutedFg} ${themeClasses.hover} hover:${themeClasses.themeFg} rounded-lg transition-colors`}
                    >
                        <LogOut className={`${isCompact ? 'h-3 w-3 mr-1' : 'h-4 w-4 mr-2'}`} />
                        <span className={isCompact ? 'text-xs' : ''}>{t('logout')}</span>
                    </button>
                </div>
            </div>

            {/* Main content area with theme support */}
            <div className="flex-1 flex flex-col overflow-hidden lg:ml-0">
                {/* Top bar with theme support */}
                <div className={`${themeClasses.themeCard} shadow-sm border-b ${themeClasses.themeBorder} flex-shrink-0 theme-transition`}>
                    <div className={`flex items-center justify-between ${isCompact ? 'h-10 px-4' : 'h-16 px-4 sm:px-6 lg:px-8'}`}>
                        <button
                            onClick={() => setSidebarOpen(true)}
                            className={`lg:hidden p-2 rounded-md ${themeClasses.themeMutedFg} ${themeClasses.hover} hover:${themeClasses.themeFg} transition-colors`}
                        >
                            <Menu className="h-5 w-5" />
                        </button>

                        <div className="flex items-center space-x-4">
                            <div className="hidden sm:block">
                                <p className={`${isCompact ? 'text-xs' : themeClasses.textSm} ${themeClasses.themeMutedFg}`}>
                                    {t('welcomeBack', { name: user.firstName })}
                                </p>
                            </div>
                        </div>
                    </div>
                </div>

                {/* Page content with theme support */}
                <main className={`flex-1 overflow-y-auto ${themeClasses.themeBg} theme-transition`}>
                    {children}
                </main>
            </div>
        </div>
    );
};

export default OrganizerClientLayout;